.hamburger {
  display: none;
  position: relative;
  width: 25px;
  height: 20px;
  z-index: 999;
  cursor: pointer;
  user-select: none;
  transition: all 0.4s ease-in;
  color: #ffffff;
  user-select: none; }
  @media (max-width: 992px) {
    .hamburger {
      display: block; } }
  .hamburger__line {
    position: absolute;
    top: 9px;
    left: 0;
    width: 100%;
    height: 2.4px;
    border-radius: 5px;
    background-color: #ffffff;
    z-index: 8;
    transition: all 0.3s ease; }

.header__hamburger:before {
  content: "";
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 2.4px;
  border-radius: 5px;
  background-color: #ffffff;
  transition: 0.3s ease; }

.header__hamburger:after {
  content: "";
  display: inline-block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2.4px;
  border-radius: 5px;
  background-color: #ffffff;
  transition: 0.3s ease; }

.toggle .hamburger__line {
  display: none; }

.hamburger.toggle::after {
  transform: rotate(-45deg) translate(2px, -10px); }

.hamburger.toggle::before {
  transform: rotate(45deg) translate(3px, 11px); }

