  @media (max-width: 992px) {
    .features__row {
      justify-content: center; } }

.features__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-right: var(--gutter);
  margin-left: var(--gutter);
  width: calc(100% * 0.31667 - var(--gutter) * 2); }
  @media (max-width: 992px) {
    .features__item {
      width: calc(100% * 0.66667 - var(--gutter) * 2);
      margin-bottom: 30px; } }
  @media (max-width: 640px) {
    .features__item {
      width: calc(100% * 1 - var(--gutter) * 2);
      margin-bottom: 30px; } }

.features__item-image {
  max-width: 100px; }

.partners__inner {
  padding: 80px 0; }

.partners__title::before {
  content: url("../img/section-logo.svg");
  display: inline-block;
  vertical-align: middle;
  padding-left: 50px;
  transform: rotate(180deg); }

.partners__items {
  display: flex;
  flex-wrap: wrap;
  justify-content: center; }

.partners__item {
  width: calc(100% * 0.25 - var(--gutter) * 2);
  padding: 30px 66px;
  width: 25%;
  border-right: 1px solid var(--bottom-border-color); }
  .partners__item:nth-child(-n + 4) {
    border-bottom: 1px solid var(--bottom-border-color); }
  .partners__item:nth-child(4n) {
    border-right: 1px solid transparent; }
  @media (max-width: 992px) {
    .partners__item {
      width: calc(100% * 0.5 - var(--gutter) * 2);
      border-bottom: 1px solid var(--bottom-border-color);
      border-right: 1px solid var(--bottom-border-color); }
      .partners__item:nth-child(even) {
        border-right: 1px solid transparent; }
      .partners__item:nth-child(n + 7) {
        border-bottom: 1px solid transparent; } }
  @media (max-width: 640px) {
    .partners__item {
      width: calc(100% * 1 - var(--gutter) * 2);
      border-bottom: 1px solid var(--bottom-border-color);
      border-right: 1px solid transparent; }
      .partners__item:nth-child(n + 7) {
        border-bottom: 1px solid var(--bottom-border-color); } }

.programm__inner {
  padding-top: 100px;
  padding-bottom: 115px; }

.programm__box {
  display: flex;
  flex-direction: column;
  align-items: center; }

.programm__title {
  position: relative;
  margin-bottom: 30px; }
  .programm__title::before {
    display: flex;
    justify-content: center;
    align-items: center;
    content: url("../img/section-logo.svg");
    margin-bottom: 50px;
    transform: rotate(135deg); }

.programm__subtitle {
  text-align: center;
  max-width: 410px;
  font-weight: normal;
  font-size: 16px;
  line-height: 25.6px;
  opacity: 0.5; }

.programm__items {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center; }
  .programm__items::before {
    position: absolute;
    content: "";
    height: 100%;
    width: 6px;
    background: var(--gradient);
    z-index: 2; }

.programm__item-wrap {
  width: calc(100% * 0.66667 - var(--gutter) * 2); }
  @media (max-width: 992px) {
    .programm__item-wrap {
      width: calc(100% * 0.83333 - var(--gutter) * 2); } }
  @media (max-width: 640px) {
    .programm__item-wrap {
      width: calc(100% * 1 - var(--gutter) * 2); } }
  .programm__item-wrap:nth-child(odd) > .programm__item {
    position: relative;
    align-items: flex-end;
    text-align: right;
    margin-right: 50%;
    padding-right: 100px; }
    .programm__item-wrap:nth-child(odd) > .programm__item::before {
      content: "";
      position: absolute;
      right: 0;
      height: 1px;
      width: 40px;
      background: rgba(255, 255, 255, 0.3); }
    @media (max-width: 992px) {
      .programm__item-wrap:nth-child(odd) > .programm__item {
        padding-right: 75px; } }
    @media (max-width: 640px) {
      .programm__item-wrap:nth-child(odd) > .programm__item {
        padding-right: 50px; } }
  .programm__item-wrap:nth-child(even) > .programm__item {
    position: relative;
    padding-left: 100px;
    margin-left: 50%; }
    .programm__item-wrap:nth-child(even) > .programm__item::before {
      content: "";
      position: absolute;
      left: 0;
      height: 1px;
      width: 40px;
      background: rgba(255, 255, 255, 0.3); }
    @media (max-width: 992px) {
      .programm__item-wrap:nth-child(even) > .programm__item {
        padding-left: 75px; } }
    @media (max-width: 640px) {
      .programm__item-wrap:nth-child(even) > .programm__item {
        padding-left: 50px; } }

.programm__item {
  display: flex;
  flex-direction: column; }

.programm__item-title {
  font-weight: normal;
  font-size: 14px;
  line-height: 22.4px;
  opacity: 0.5; }

.programm__item-descr {
  font-weight: normal;
  font-size: 18px;
  line-height: 160%; }
  @media (max-width: 640px) {
    .programm__item-descr {
      font-size: 14px; } }

.teachers__inner {
  padding-top: 115px;
  padding-bottom: 100px; }

.teachers__title::before {
  content: url("../img/section-logo.svg");
  display: inline-block;
  padding-right: 30px;
  transform: rotate(317deg); }

.teachers__items {
  padding-top: 75px; }

.teachers__item {
  margin-right: var(--gutter);
  margin-left: var(--gutter);
  width: calc(100% * 0.33333 - var(--gutter) * 2);
  display: flex;
  flex-direction: column;
  align-items: center; }
  @media (max-width: 992px) {
    .teachers__item {
      width: calc(100% * 1 - var(--gutter) * 2);
      margin-bottom: 50px; } }

.teachers__item-name {
  margin: 15px 0;
  font-weight: 500;
  font-size: 24px;
  line-height: 33.6px; }

.teachers__item-spec {
  margin-top: 0;
  font-weight: normal;
  font-size: 16px;
  line-height: 25.6px;
  opacity: 0.5; }

.teachers__item-button {
  text-decoration: none;
  border: 1px solid var(--primary-text-color); }

.footer {
  flex-shrink: 0;
  padding: 100px 0; }
  .footer__inner {
    display: flex;
    flex-direction: column;
    align-items: center; }
  .footer__title {
    margin-top: 0;
    margin-bottom: 30px; }
  .footer__descr {
    text-align: center;
    max-width: 400px;
    margin-top: 0;
    margin-bottom: 60px;
    font-weight: normal;
    font-size: 14px;
    line-height: 25.6px;
    opacity: 0.5; }
  .footer__form {
    position: relative;
    margin-bottom: 40px; }
    @media (max-width: 640px) {
      .footer__form {
        width: 80%;
        display: flex;
        flex-direction: column;
        align-items: center; } }
  .footer__input {
    width: 370px;
    padding: 10px 40% 10px 20px;
    background-color: rgba(255, 255, 255, 0.25);
    border-radius: 50px 0px 0px 50px;
    border: none;
    outline: none; }
    @media (max-width: 640px) {
      .footer__input {
        width: 100%;
        margin-bottom: 30px;
        border-radius: 50px; } }
  .footer__button {
    position: absolute;
    top: 0;
    right: -5%;
    background: linear-gradient(94.78deg, #DF5950 11.19%, #451046 93.72%); }
    @media (max-width: 640px) {
      .footer__button {
        position: static; } }
  .footer__socials {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 30px; }
  .footer__socials-item {
    width: 50px;
    height: 50px;
    list-style-type: none;
    border-radius: 50%;
    background: linear-gradient(94.26deg, #C89AFC 9.51%, #7C6AFA 90.23%); }
    .footer__socials-item:nth-of-type(1) a {
      background: no-repeat center center url(../img/socials/vk.svg); }
    .footer__socials-item:nth-of-type(2) a {
      background: no-repeat center center url(../img/socials/youtube.svg); }
    .footer__socials-item:nth-of-type(3) a {
      background: no-repeat center center url(../img/socials/facebook.svg); }
    .footer__socials-item:nth-of-type(4) a {
      background: no-repeat center center url(../img/socials/instagram.svg); }
  .footer__socials-link {
    width: 100%;
    height: 100%;
    display: block;
    text-decoration: none; }
